

h1,
h2 {
  margin-top: 10px;
}

h1 {
  margin-bottom: 16px;
  font-size: 24px;
  font-weight: 700;
}

h2 {
  margin-bottom: 12px;
  font-size: 18px;
  font-weight: 400;
}

/*- Card container -*/
.card-container {
  position: relative;
  -webkit-text-decoration: white !important;
          text-decoration: white !important;
  z-index: 99;
  margin: 32px auto;
  margin-top: 150px;
  max-width: 720px;
  height: 420px;
  -webkit-perspective: 1100px;
          perspective: 1100px;
        
}

.card-side {
   background-color: grey !important;
   background-image: url('./fondCard.png');
   color: white !important;
   border: 1.5px solid grey;

}
img {
  max-width: 100%;
  max-height: 100%;
}

/*- Card body -*/
.card-body {
  width: 100%;
  height: 100%;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: all .7s linear;
  -o-transition: all .7s linear;
  transition: all .7s linear;
  
  
}

/*- Flipping the card -*/
.card-container:hover .card-body {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card-container:hover > .card-body > .side-front {
  opacity: 0;
  visibility: hidden;
  -webkit-transition: opacity 1s ease-in, visibility .75s linear;
  -o-transition: opacity 1s ease-in, visibility .75s linear;
  transition: opacity 1s ease-in, visibility .75s linear;
}

/*- Card sides -*/
.card-side {
  position: absolute;
  border-radius: 10px;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
  color: #212121;
  background-color: #fff;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-box-shadow: 0 10px 35px rgba(50, 50, 93, 0.1), 0 2px 15px rgba(0, 0, 0, 0.07);
          box-shadow: 0 10px 35px rgba(50, 50, 93, 0.1), 0 2px 15px rgba(0, 0, 0, 0.07);
}

/*- Front side -*/
.side-front [class^=col-xs]:first-of-type {
 -ms-flex-pack: center;
     justify-content: center;
-ms-flex-line-pack: center;
    align-content: center;
}

.side-front-content {
  padding-top: 32px;
  padding-right: 32px;
  padding-bottom: 32px;

}

/*- Back side -*/
.side-back {
  z-index: 2;
  padding: 32px;
  text-align: center;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

/*- Form -*/
.card-form {
  margin-top: 32px;
}

.card-form .row + .row,
.card-form .row + fieldset,
.card-form fieldset + fieldset {
  margin-top: 16px;
}

input,
textarea {
  padding: 8px;
  width: 100%;
  border-top: 0;
  border-right: 0;
  border-bottom: 1px solid #eee;
  border-left: 0;
}
input:focus,
textarea:focus {
  outline: 0;
  border-bottom: 1px solid #0c81f6;
}

textarea {
  max-height: 80px;
  resize: vertical;
}

.btn-primary {
  padding: 8px 16px;
  font-size: 22px;
  background-color: #0c81f6;
  border: none;
  -webkit-box-shadow: 0 10px 35px rgba(50, 50, 93, 0.1), 0 2px 15px rgba(0, 0, 0, 0.07);
          box-shadow: 0 10px 35px rgba(50, 50, 93, 0.1), 0 2px 15px rgba(0, 0, 0, 0.07);
  -webkit-transition: background-color .25s ease-in, -webkit-box-shadow .25s ease-in;
  transition: background-color .25s ease-in, -webkit-box-shadow .25s ease-in;
  -o-transition: background-color .25s ease-in, box-shadow .25s ease-in;
  transition: background-color .25s ease-in, box-shadow .25s ease-in;
  transition: background-color .25s ease-in, box-shadow .25s ease-in, -webkit-box-shadow .25s ease-in;
}
.btn-primary:focus, .btn-primary:hover {
  background-color: #56a7f9;
  -webkit-box-shadow: 0 18px 35px rgba(50, 50, 93, 0.1), 0 8px 15px rgba(0, 0, 0, 0.07);
          box-shadow: 0 18px 35px rgba(50, 50, 93, 0.1), 0 8px 15px rgba(0, 0, 0, 0.07);
}

/*- Social links -*/
.social-links {
    font-size: 35px;
  padding: 0;
  margin-top: 15px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  list-style-type: none;
}
.social-links li:nth-of-type(n+2) {
  margin-left: 24px;

}
.social-links a {
  color: #212121;
  -webkit-transition: opacity .25s ease-in;
  -o-transition: opacity .25s ease-in;
  transition: opacity .25s ease-in;
  
}
.social-links a:focus, .social-links a:hover {
  opacity: .5;
  
}
.social-links i {
  font-size: 24px;
  
  
}



